export default "@import url(\"https://fonts.googleapis.com/css?family=Roboto:400,400i,700\");\nion-content {\n  --background: #ffffff;\n}\nion-content ion-card {\n  --background: #ffffff;\n}\nion-header ion-item {\n  --background:radial-gradient(#F49B27, #ED7F23);\n  background: radial-gradient(#F49B27, #ED7F23);\n  padding: 0 10px;\n}\nion-header ion-item h3 {\n  font-weight: 700;\n  font-size: 14px;\n  color: #fff;\n}\nion-header ion-item p {\n  font-weight: 500;\n  font-size: 14px;\n  color: #fff;\n}\nion-header ion-item ion-avatar {\n  --border-radius: 40%;\n}\nion-fab-button {\n  --background: #F49B27;\n}\nbody {\n  font-family: Roboto, sans-serif;\n  margin: 0;\n  height: 100vh;\n  display: grid;\n  align-items: center;\n  justify-items: center;\n  background-image: linear-gradient(to top, #96fbc4 0%, #f9f586 100%);\n}\n.card {\n  background: #fff;\n  border-radius: 4px;\n  max-width: 400px;\n  display: flex;\n  flex-direction: row;\n  border-radius: 25px;\n  position: relative;\n}\n.card h2 {\n  margin: 0;\n  padding: 0 1rem;\n}\n.card .title {\n  padding: 1rem;\n  text-align: right;\n  color: #ED7F23;\n  font-weight: bold;\n  font-size: 12px;\n}\n.card .desc {\n  padding: 0.5rem 1rem;\n  font-size: 12px;\n}\n.card .actions {\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  align-items: center;\n  padding: 0.5rem 1rem;\n}\n.img-avatar {\n  width: 80px;\n  height: 80px;\n  position: absolute;\n  border-radius: 50%;\n  border: 6px solid white;\n  background: radial-gradient(#F49B27, #ED7F23);\n  top: 15px;\n  left: 85px;\n}\n.card-text {\n  display: grid;\n  grid-template-columns: 1fr 2fr;\n}\n.title-total {\n  padding: 2.5em 1.5em 1.5em 1.5em;\n}\npath {\n  fill: white;\n}\n.img-portada {\n  width: 100%;\n}\n.portada {\n  width: 100%;\n  height: 100%;\n  border-top-left-radius: 20px;\n  border-bottom-left-radius: 20px;\n  background: radial-gradient(#F49B27, #ED7F23);\n  background-position: bottom center;\n  background-size: cover;\n}\nbutton {\n  border: none;\n  background: none;\n  font-size: 24px;\n  color: #8bc34a;\n  cursor: pointer;\n  transition: 0.5s;\n}\nbutton:hover {\n  color: #4CAF50;\n  transform: rotate(22deg);\n}\n/*# sourceMappingURL=data:application/json;base64, */";